@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
body{box-sizing:border-box;font-family:"Baloo Tamma 2", sans-serif}html{line-height:1.15;-webkit-text-size-adjust:100%}body{margin:0}main{display:block}h1{font-size:2em;margin:0.67em 0}a{background-color:transparent}button,input,optgroup,select,textarea{font-family:inherit;font-size:100%;line-height:1.15;margin:0}button,input{overflow:visible}button,select{text-transform:none}button,[type="button"],[type="reset"],[type="submit"]{-webkit-appearance:button}button::-moz-focus-inner,[type="button"]::-moz-focus-inner,[type="reset"]::-moz-focus-inner,[type="submit"]::-moz-focus-inner{border-style:none;padding:0}button:-moz-focusring,[type="button"]:-moz-focusring,[type="reset"]:-moz-focusring,[type="submit"]:-moz-focusring{outline:1px dotted ButtonText}::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}.header{display:none;align-items:center;justify-content:flex-end;box-shadow:0px 1px 5px #111111;background-color:#4f2991;margin-bottom:1em;padding:1rem;color:white}.header #title{margin-right:auto;margin-top:0;margin-bottom:0}.header #github{display:block;transition:all 100ms;color:rgba(255,255,255,0.8)}.header #github:hover,.header #github:focus{color:#fff}main{display:none;width:95%;height:500px;margin:0 auto;max-width:1500px}main #canvas_container,main #Menu{flex-basis:50%}#canvas_container{box-shadow:0px 0px 1px black}#canvas{width:100%;height:100%;display:block}.Brush{cursor:url(/MywebpaintReact/static/media/pincel.1f27b1c5.ico),auto}.Eraser{cursor:url(/MywebpaintReact/static/media/borradorico.db38ff51.ico),auto}#Menu{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}#Menu button{display:block;color:white;font-weight:bold;padding:0.3rem;box-shadow:0px 5px rgba(0,0,0,0.5);transition:100ms all;font-size:larger;border:5px solid transparent;border-radius:1rem;width:80%;cursor:pointer}#Menu button:active{transform:translate(0px, 5px);box-shadow:0px 0px black;background-color:none}#Menu button:focus{outline:none;border-color:rgba(255,255,255,0.3)}#Menu button:disabled{background-color:black;cursor:not-allowed;transform:translate(0px, 5px);box-shadow:none;border-color:transparent}#red{background-color:#f8373b}#green{background-color:#909d47}#blue{background-color:#343497}#help,#limpiar,#limpiar_partes{background-color:#1f1616}.Absolute{top:0;position:absolute;width:600px;max-width:100%;position:absolute;left:50%;top:5%;transform:translate(-50%, -5%);width:50%}.MyModal{box-shadow:0px 0px 1px #1f1616;background-color:white;color:#1f1616;border-radius:.2em}.MyModal .MyModal-body{display:flex;flex-flow:column}.MyModal .MyModal-body .header-flex{display:flex;justify-content:space-between;align-items:center;padding:0.3em;height:40px;border-bottom:0.5px solid #1111111f;color:#111111}.MyModal .MyModal-body .header-flex h3{font-size:25px;margin:0}.MyModal .MyModal-body .header-flex #header_exit{opacity:0.6;cursor:pointer;font-size:25px;transition:opacity 500ms}.MyModal .MyModal-body .header-flex #header_exit:hover,.MyModal .MyModal-body .header-flex #header_exit:focus{opacity:1}.MyModal .section-flex{display:flex;flex-direction:column;padding:0.3em;font-family:barlow, sans-serif}.MyModal #exit{background-color:#4caf4fd5;border:none;color:white;padding:10px 25px;text-align:center;text-decoration:none;display:inline-block;font-size:16px;cursor:pointer;transition:background-color 500ms}.MyModal #exit:hover,.MyModal #exit:focus{background-color:#4CAF50}#announcement{display:flex;justify-content:center;align-items:center;height:100vh;text-align:center}@media screen and (min-width: 768px){#announcement{display:none}.header,main{display:flex}}

